<template lang='pug' src='./clear-aligners.pug'></template>

<script>
import Loader from 'components/loader/loader'
import Header from 'components/header/header'
import Footer from 'components/footer/footer'

export default {
  name: 'clear-aligners',
  computed: {
    props () {
      return this.$store.state.pages['clear-aligners']
    },
    loading () {
      return this.$store.state.loading
    }
  },
  components: {
    Loader,
    Header,
    Footer
  }
}
</script>



